@import "../../sassStyles/_variables";

#pagesContainer {
  position: relative;
  width: calc(100% - 6vw);
  height: 100%;
  margin-left: 6vw;
  transition: 1s cubic-bezier(.86,0,.07,1);
  overflow: hidden;
}

.page {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.content {
  height: 75%;
  width: 85%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-40%, -40%) rotate(5deg) scale(0.8);
  box-shadow: 1px 1px 1px 0px $dark-color-palette;
  background-color: $primary-middle-color-palette;
  padding: 20px;
  font-size: 4vw;
  text-align: center;
  transition: 0.5s ease-in-out;

  .title {
    transform: translateX(calc(-100%));
    transition: 0.7s ease-in-out 0.2s;
    opacity: 0;
  }

  &.animate {
    transform: translate(-50%, -50%);

    .title {
      transform: none;
      opacity: 1;
    }

  }
}